.hero {
    background: linear-gradient(to bottom right,rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0)), 
    url('../../public/img/pianoBackground.jpg');
  //  background-image: url('../../public/img/pianoBackground.jpg');
    background-size:cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 500px;
    color: #ffff;    
}
.heroBlock{
    position: relative;
    top:150px;
    padding: 0 40px;
    
}
.heroText {
    h1{
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
    }
    p{
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.9);
        font-size: 16px;
    }
    .mobile {
        display: none;
    }
}

.middle {
    margin-top: 0px;
    gap: 40px;
    p {
        margin: 5px;
    }
}
.middleCard {
    background-color: rgba(255, 255, 255, 0.9);
    color: black;
    border-radius: 10px;
    box-shadow: 2px 2px 4px 0px rgba(232, 230, 230, 0.6);
    padding: 10px;
    strong {
        font-size: 18px;
    }
    img {
        filter: invert(62%) sepia(93%) saturate(430%) hue-rotate(10deg) brightness(90%) contrast(90%);
    }
}


.services {
    //height: 400px;
    background-color: rgba(226, 226, 226, 0.6);
}
.serviceText{
    display: flex;
    flex-direction: column;
    align-items: start;
    max-width: 80%;
    margin-left: 10%;
    p {
        color: #343434;
        font-size: 18px;
        font-weight: 500;
        text-indent: 40px;
    }
}
.serviceContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
}

.serviceCard {
    background: #f8f9fa;
    padding: 20px;
    border-radius: 10px;
    width: 250px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.serviceCard h3 {
    margin-bottom: 10px;
}

.serviceCard p {
    font-size: 14px;
    color: #555;
}

.aboutSection {
    padding: 50px 20px;
    background-color: #f8f9fa;
    text-align: center;
}

.aboutContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 900px;
    margin: 0 auto;
}

.aboutImage {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.aboutText {
    margin-left: 20px;
    max-width: 500px;
    text-align: left;
}

.aboutText h2 {
    font-size: 28px;
    margin-bottom: 10px;
}

.aboutText p {
    font-size: 16px;
    color: #555;
    line-height: 1.5;
}

.pricingSection {
    padding: 50px 20px;
    background-color: rgba(226, 226, 226, 0.6);
    text-align: center;
}

.pricingTitle {
    font-size: 28px;
    margin-bottom: 20px;
}

.pricingList {
    list-style: none;
    padding: 0;
    max-width: 900px;
    margin: 0 auto;
}

.pricingList li {
    display: flex;
    justify-content: space-between;
    padding: 12px 20px;
    font-size: 18px;
    border-bottom: 1px solid #808080;
}

.serviceName {
    font-weight: 500;
    color: #333;
}

.servicePrice {
    font-weight: bold;
    color: #d35400;
    font-size: 18px;
}

@media screen and (max-width: 768px){
    h2 {
        font-size: 24px;
    }
    .heroBlock {
        position: relative;
        top: 70px;
        padding: 0 20px;
        
    }

    .heroText {
        h1{
            font-size: 20px;
        }
        p{       
            font-size: 14px;
        }
        .mobile {
            display: block;
        }
        .desktop {
            display: none;
        }
    }
    .middle {
        flex-direction: column;
        align-items: baseline;
        gap: 10px;
    }
    .middleCard {
        padding: 5px;
        width: 95%;
        img {
            width: 20px;
            height: 20px;
        }
        p {
          font-size: 12px; 
          margin: 0;
        }
    }


    .serviceText{
        display: flex;
        flex-direction: column;
        align-items: start;
        max-width: 100%;
        margin-left: 5%;
        p {
            color: #343434;
            font-size: 18px;
            font-weight: 500;
            text-indent: 40px;
        }
    }
    .serviceContainer {
      flex-direction: column;
      align-items: center;
    }

    .aboutText {
        .aText {
            text-align: justify
        }
    }
    
    .pricingTitle {
        //font-size: 24px;
        margin-top: 0;
        margin-bottom: 20px;
    }
    
    .serviceName {
        font-size: 14px;
    }
    
    .servicePrice {
        font-size: 14px;
    }
    
}